<template>
  <div>
    <TodoHeader></TodoHeader>
    <TodoInput @tossTodoText="tossText"></TodoInput>
    <TodoList @removeItem="removeItem" :propsdata="arrTodoItems"></TodoList>
    <TodoFooter @clearAllItem="clearAllItem"></TodoFooter>
  </div>
</template>

<script>
import TodoHeader from "./components/TodoHeader.vue";
import TodoInput from "./components/TodoInput.vue";
import TodoList from "./components/TodoList.vue";
import TodoFooter from "./components/TodoFooter.vue";

export default {
  created() {
    if(localStorage.length > 0) {
      for(var i = 0; i < localStorage.length; i++) {
        if(localStorage.key(i) !== "loglevel:webpack-dev-server") {
          this.arrTodoItems.push(localStorage.key(i));
        }
      }
    }
  },
  methods: {
    tossText(value) {
      this.arrTodoItems.push(value);
    },
    removeItem(payload) {
      localStorage.removeItem(payload.item);
      this.arrTodoItems.splice(payload.index, 1);
    },
    clearAllItem() {
      this.arrTodoItems = [];
      localStorage.clear();
    }
  },
  data() {
    return {
      arrTodoItems: [],
    }
  },
  components: {
    TodoHeader,
    TodoInput,
    TodoList,
    TodoFooter
  }
}
</script>

