<template>
  <div>
    <input type="text" v-model="todoText" @keydown.enter="addTodoText">
    <button @click="addTodoText">add</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      todoText: "",
    }
  },
  methods: {
    addTodoText() {
      localStorage.setItem(this.todoText, this.todoText);
      this.$emit("tossTodoText", this.todoText);
      this.todoText = "";
    }
  }
}
</script>

