<template>
  <div>
    <ul>
      <li v-for="(item, index) in propsdata" :key="item">
        {{ index+1 }}. {{ item }}
        <button @click="removeTodo(item, index)">X</button>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: ["propsdata"],
  methods: {
    removeTodo(item, index) {
      this.$emit("removeItem", {item, index});
    }
  }
}
</script>

<style>
  button {cursor: pointer;}
  ul {margin: 0; padding: 0;}
  li {margin: 10px 0; padding: 0; list-style: none;}
</style>
